  @keyframes burger {
    0% {
   
    transform: translateX(100%);
    }
    100% {
      transform: translateX(0);
    }
  }


  
  @keyframes slide-bottom {
    0% {
              transform: translateY(-100px);
              opacity: 0;       
    }

    30% {
              transform: translateY(-20px);
              opacity: 0;         
    }

    100% {
              transform: translateY(0px);
              opacity: 1;
    }
  }

  .featchest_animation {
      &:nth-child(1) {
        @include featchest_animation();
   
      }

      &:nth-child(2) {
        @include featchest_animation();
             animation-delay: 1s;
      }

      &:nth-child(3) {
        @include featchest_animation();
        animation-delay: 2s;
      }
  }



