.cookie {

    @include xs-block {
        width: 100vw;
        background: #FFFFFF;
        position: fixed;
        bottom: 0;
        height: 12vh;
    }
    @include lg-block {
        height: 14.8vh;
    }

    

}

.cookie_content {
    height: 100%;
    display: flex;
    
    margin: 0 auto;
    align-items: center;

    @include xs-block {
        justify-content: space-between;
        flex-direction: row;
        width: 90%;
    }
    @include lg-block {
        flex-direction: row;
        width: rem(944);
    }
    @include xl-block {
        width: rem(1244);
    }

    .text {

    
        color: #000;
        
        line-height: rem(22);
        letter-spacing: -0.337647px;
        margin: 0;

        @include xs-block {
            line-height: rem(18);
            text-align: left;
            font-size: rem(14);
            width: 85%;
        }

        @include lg-block {
            line-height: rem(22);
            text-align: left;
            width: 90%;
            font-size: rem(18);
        }

        h4{
            @include xs-block {
                font-weight: 500;
            }
        }

        a {
            color: #0072FF;
            &:hover {
                text-decoration: underline;
            }     
        }

        p {
            margin: 0;
        }
    }
    .cookie_toggle {
        position: relative;
        top: rem(0);

        @include xs-block {
            padding: 10px 10px;
            img {
                width: 17px;
                height: auto;
            }
            
        }
        @include lg-block {
            top: rem(5);
            padding: 10px 30px;
            
        }
        cursor: pointer;
    }
}



